import React from 'react';
import { HomepageBanner, HomepageCallout } from 'gatsby-theme-carbon';
import HomepageTemplate from 'gatsby-theme-carbon/src/templates/Homepage';
import homepageStyles from './Homepage.module.scss';

import Carbon from '../../images/mask-phone-app-slight-color-cropped.jpg';

const FirstLeftText = () => <p>WNTRAC</p>;

const FirstRightText = () => (
  <p>
  A comprehensive dataset of over 7,000 non-pharmaceutical interventions implemented in response to COVID-19 by governments worldwide, covering 261 geographical regions and frequently updated to ensure the most up-to-date information.
  </p>
);

const BannerText = () => (
    <div className={homepageStyles.homepageBannerTextContainer}>
        <h2 className={homepageStyles.homepageBannerText}>Worldwide Non-pharmaceutical Interventions Tracker for COVID-19
            <span className={homepageStyles.homepageBannerTextAbbreviation}>(WNTRAC)</span>
        </h2>
    </div>
);

const customProps = {
  Banner: <HomepageBanner renderText={BannerText} image={Carbon} />,
  FirstCallout: (
    <HomepageCallout
      backgroundColor="#061f80"
      color="white"
      leftText={FirstLeftText}
      rightText={FirstRightText}
    />
  ),
  SecondCallout: null,
};

// spreading the original props gives us props.children (mdx content)
function ShadowedHomepage(props) {
  return <HomepageTemplate {...props} {...customProps} />;
}

export default ShadowedHomepage;
